@keyframes fadeRotateY {
  from {
    opacity: 0;
    transform: rotateY(180deg);
  }
  to {
    opacity: 1;
    transform: rotateY(0);
  }
}
@keyframes fadeSlideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeScaleX {
  from {
    opacity: 0;
    transform: scaleX(-1);
  }
  to {
    opacity: 1;
    transform: scaleX(1);
  }
}
.hero-banner {
  position: relative;
  height: calc(80vh + 2rem);
  width: 100%;
  overflow: hidden;
}
.hero-banner .background-static {
  position: absolute;
  inset: 0;
  background-color: var(--bg-color);
  background-image: radial-gradient(rgba(var(--text-color), 0.02) 1px, transparent 0);
  background-size: 2px 2px;
  z-index: 0;
  filter: brightness(1.2) contrast(1.1) saturate(1.2);
}
.hero-banner .overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.35);
  -webkit-backdrop-filter: blur(3px) brightness(0.75);
  backdrop-filter: blur(3px) brightness(0.75);
  z-index: 1;
}
.hero-banner::before {
  content: "";
  position: absolute;
  bottom: 15%;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  height: 40%;
  background: radial-gradient(ellipse at center, rgba(var(--accent-color), 0.1), transparent 70%);
  z-index: 1;
}
.hero-banner::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image: radial-gradient(rgba(var(--text-color), 0.02) 1px, transparent 0);
  opacity: 0.03;
  pointer-events: none;
  z-index: 1;
}
.hero-banner .text-content {
  position: relative;
  z-index: 2;
  width: 100%;
  max-width: 95%;
  margin: 0 auto;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Merriweather", serif;
  color: var(--accent-color);
  gap: 2.5rem;
  transform: translateY(10rem);
}
@media (max-width: 1024px) {
  .hero-banner .text-content {
    transform: translateY(14rem);
  }
}
.hero-banner .text-content h2.domino {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.3rem;
  font-size: clamp(1.2rem, 4vw, 2.2rem);
  text-align: center;
  color: var(--accent-color);
  text-shadow: 0 0 6px rgba(var(--accent-color), 0.5);
  word-break: keep-all;
  overflow-wrap: break-word;
}
.hero-banner .text-content h2.domino span {
